@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;  
  box-sizing: border-box;
}
.app{
  min-height: 94.5vh;
  border: 8px solid #39445a93;
  /* background-color: white; */
  margin: 5px;
  padding: 5px;
  /* min-height: 98.5vh; */
}
a{
  text-decoration: inherit;
  color: inherit;
  /* color: red; */
}
.header {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .title {
    text-transform: uppercase;
    font-size: 6vw;
    font-family: "Montserrat", sans-serif;
    cursor: pointer;  
  }
  .divider {
    margin: 10px;
    width: 100%;
    background-color: gray;
  }
  
.error{
    text-align: center;
    background-color: #ca0a1a;
    margin-bottom: 8px;
    color: beige;
    border-radius: 4px;
    padding: 6px;
}
.content{
    display: flex;
    justify-content: space-around; 
    /* flex-direction: column-reverse; */
    }
    .settings{
        width: 25%;
        padding: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-family: "Poppins", sans-serif;
        font-weight: 30;
        /* margin-bottom: 20rem; */
    }
    .banner{
        width: 55%;
        align-self: center;
        padding: 8px;
    }
    @media (max-width:800px){
        .content{
            flex-direction: column-reverse;
            align-items: center;
        }
        .banner{
            width: 85%;
            padding: 0;
            padding-top: 20px;
        }
        .settings{
            width: 100%;
        }
    }
    .settings_select{
        display: flex;
        flex-direction: column;
    }
.question {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .singleQuestion {
    width: 95%;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 5px solid grey;
    padding: 20px;
    margin-top: 10px;
  }
  
  .options {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex: 1 1;
    align-items: center;
    justify-content: space-around;
    margin: 10px;
  }
  
  .singleOption {
    width: 46%;
    height: 50px;
    padding: 15px 20px;
    margin: 10px;
    box-shadow: 0 0 2px black;
  }
  
  @media (max-width: 614px) {
    .singleOption {
      width: 100%;
    }
  }
  
  .controls {
    display: flex;
    width: 100%;
    justify-content: space-around;
  }
  
  .select {
    background-color: rgb(7, 207, 0);
    color: white;
    box-shadow: 0 0 1px black;
  }
  
  .wrong {
    background-color: rgb(233, 0, 0);
    color: white;
    box-shadow: 0 0 1px black;
  }
.quiz{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Poppins',sans-serif;
}
.subtitle{
    font-size: 2.5rem;
    border: 1px solid black;
    box-shadow: 4px 4px 2px black;
    padding: 5px 10px;
}
.quizInfo{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 10px;
    text-transform: uppercase;
}
.result{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60vh;
    flex-direction: column;
    }
